import React, {useEffect, useRef, useState} from 'react';
import {Button, useImageViewer, useViewportGrid} from '@ohif/ui'
import {ToolGroupManager, utilities as cstUtilities } from '@cornerstonejs/tools';
import ProgressBar from '@ramonak/react-progress-bar';
import {metaData} from '@cornerstonejs/core';

import {createControlPoints, createPanoramic} from './PanoramicUtils/generatePanoramic'
import {dcmbufferToPng, pngToCanvas} from './PanoramicUtils/pngUtils.js'

const skmeans = require('skmeans')

function waitCondition(condition) {
    return new Promise((resolve, reject) => {
        const wait = () => {
            const result = condition();
            if (result) {
                return resolve(result);
            } else {
                setTimeout(() => wait(), 50);
            }
        }
        wait();
    });
}

export default function PanoramicGenerationPanel({
                                           commandsManager,
                                           extensionManager,
                                           servicesManager
                                       }): React.FunctionComponent {
    // @ts-ignore
    const {
        cornerstoneViewportService,
        hangingProtocolService,
        measurementService,
        panoramicService,
    } = servicesManager.services;

    const getDisplaySetInstanceUID = () => {
        return cornerstoneViewportService.getViewportInfo('default').viewportData.data[0].displaySetInstanceUID;
    }

    const [panoramicState, setPanoramicState] = useState(
        panoramicService.getPanoramicState(getDisplaySetInstanceUID()) ?? null
    );

    // Progress bar for panoramic computation
    const [isProcessingPanoramic, setIsProcessingPanoramic] = useState(false);
    const [processingPourcent, setProcessingPourcent] = useState(0);

    // Panoramic computation settings
    const [numberBasePoints, setNumberBasePoints] = useState(11);

    useEffect(() => {
        const eventUpdated = panoramicService.EVENTS.PANORAMIC_STATE_UPDATED;
        const { unsubscribe } = panoramicService.subscribe(eventUpdated, ({ displaySetUID }) => {
            const panoramicStateStored = panoramicService.getPanoramicState(displaySetUID);
            setPanoramicState({ ...panoramicStateStored });
        });

        const onStart = async() => {
            // Ensure default viewport is axial
            await getAxialViewport();

            // Restore panoramicState
            const displaySetInstanceUID = getDisplaySetInstanceUID();
            const panoramicStateStored = panoramicService.getPanoramicState(displaySetInstanceUID);
            if (panoramicStateStored) {
                // Force re-render
                setPanoramicState(panoramicStateStored);
            }
            if (!panoramicStateStored?.panoramic?.width) {
                // Generate (control points) and/or panoramic
                computePanoramic();
            }

            // Loading is asynchronous so protocol can be panoramicViewport before the cornerstoneViewportService is updated
            // Also, the cornerstoneViewportService can be updated before the viewports are rendered
            if (hangingProtocolService.getActiveProtocol().protocol.id !== 'panoramicViewport') {
                commandsManager.runCommand('toggleHangingProtocol', { protocolId: 'panoramicViewport', stageIndex: 0 });
            }
        }
        onStart();

        return () => {
            // Unsubscribe panoramicState
            unsubscribe();

            // Delete measurements on unload
            const displaySetInstanceUID = getDisplaySetInstanceUID();
            const panoramicStateStored = panoramicService.getPanoramicState(displaySetInstanceUID);
            if (panoramicStateStored?.annotation?.uid) {
                panoramicService.setAnnotationVisible(displaySetInstanceUID, false);
            }
        }
    }, []);

    useEffect(() => {
        // Process only after panoramicState is filled
        if (panoramicState?.panoramic?.width) {
            renderPanoramic();
        }
    }, [panoramicState]);


    const getAxialViewport = async() => {
        let axialViewport = cornerstoneViewportService.getCornerstoneViewport('default');
        if (!axialViewport) {
            // Fix issue when protocol is on the focused volume3D
            // TODO, issue with priority order for protocols
            await commandsManager.runCommand('toggleOneUp');
            axialViewport = await waitCondition(() => cornerstoneViewportService.getCornerstoneViewport('default'));
        }

        return axialViewport;
    }

    const computePanoramic = async() => {
        // Display progress bar in front-end
        setIsProcessingPanoramic(true);

        // No panoramic
        const panoramicState = panoramicService.getPanoramicState(getDisplaySetInstanceUID());
        if (!panoramicState?.annotation) {
            // No control points
            await generatePanoramicControlPoints(false);
        }
        await generatePanoramicFromControlPoints(false);
    }

    const renderPanoramic = async() => {
        // Ensure protocol is correctly loaded
        await waitCondition(() => hangingProtocolService.getActiveProtocol().protocol.id === 'panoramicViewport');
        // Ensure viewports are correctly displayed
        await waitCondition(() => cornerstoneViewportService.getRenderingEngine()._needsRender.size === 0);

        const viewportIds = cornerstoneViewportService.getViewportIds();
        deletePanoramicAnnotation();
        generateNewPanoramicAnnotation(viewportIds[1]);
        displayDentascans(viewportIds);
    }

    const deletePanoramicAnnotation = () => {
        //============================= DELETE ALL PREVIOUS DENTASCAN ANNOTATIONS =================//
        const dentascanMeasurements = measurementService.getMeasurements()
            .filter(m => {return m.toolName === 'ArrowAnnotate'});

        // Delete measurements that are not handmade (thus the interpolated line)
        for (const pm of dentascanMeasurements) {
            if (pm.metadata.dentascan) {
                commandsManager.runCommand('deleteMeasurement', { uid: pm.uid });
            }
        }
    }

    const generateNewPanoramicAnnotation = (viewportId) => {
        //================== DISPLAY DENTASCAN SECTION IN PANORAMIC AS ANNOTATION ===========================//
        const panoramicViewport = cornerstoneViewportService.getCornerstoneViewport(viewportId);
        if (!panoramicViewport) {
            // Panoramic Viewport not found
            return
        }

        const initialImageIndex = panoramicState.dentascanViewportData.data[0].initialImageIndex;
        const pointUp = [initialImageIndex, 0, 0];
        const pointDown = [initialImageIndex, panoramicState.panoramic.height, 0];

        // Get ArrowAnnotate instance in order to create new annotation indicating dentascan section on panoramic
        const toolGroup = ToolGroupManager.getToolGroupForViewport(
            panoramicViewport.id,
            panoramicViewport.renderingEngineId
        );

        const arrowAnnotateInstance = toolGroup.getToolInstance('ArrowAnnotate');
        arrowAnnotateInstance.addNewAnnotationWithMode(panoramicViewport.element, [pointUp, pointDown],
            'dentascan', 'rgb(255, 255, 255)');
    }

    const displayDentascans = (viewportIds) => {
        //================== DISPLAY WANTED DENTASCANS IN BOTTOM VIEWPORT ===========================//
        const initialImageIndex = panoramicState.dentascanViewportData.data[0].initialImageIndex;

        for (let i = 2; i <= 8; i++) {
            // With i === 4 being the main dentascan, we also display two dentascans before and after
            cstUtilities.jumpToSlice(cornerstoneViewportService.getCornerstoneViewport(viewportIds[i]).element, {
                imageIndex: initialImageIndex + (i - 5) * 2,
                debounceLoading: true,
            });
        }
    }

    const refreshPanoramicDisplay = async() => {
        commandsManager.runCommand('toggleHangingProtocol', { protocolId: 'panoramicViewport', stageIndex: 0 });
        commandsManager.runCommand('toggleHangingProtocol', { protocolId: 'panoramicViewport', stageIndex: 0 });
        await waitCondition(() => {
            const dentascanViewportId = cornerstoneViewportService.getViewportIds()[2];
            return cornerstoneViewportService.getCornerstoneViewport(dentascanViewportId);
        });
    }

    // Debug function to display random selected points in bone cluster
    const drawPoint = (context, x, y, label, color, size) => {
        if (color == null) {
            color = '#000';
        }
        if (size == null) {
            size = 5;
        }
        var radius = 0.5 * size;
        // to increase smoothing for numbers with decimal part
        var pointX = Math.round(x - radius);
        var pointY = Math.round(y - radius);
        context.beginPath();
        context.fillStyle = color;
        context.fillRect(pointX, pointY, size, size);
        context.fill();
        if (label) {
            var textX = Math.round(x);
            var textY = Math.round(pointY - 5);
            context.font = 'Italic 14px Arial';
            context.fillStyle = color;
            context.textAlign = 'center';
            context.fillText(label, textX, textY);
        }
    }

    // Debug function to display image in canvas below application main canvas (cancel override-hidden-y)
    const debugShowImage = (pixelArray, rows, columns) => {
        return new Promise<HTMLCanvasElement>(async(resolve, reject) => {
            // Get volume imageId
            const viewport = await getAxialViewport();
            const imageId = viewport.getCurrentImageId();

            const voiLutModule = metaData.get('voiLutModule', imageId);
            const imagePixelModule = metaData.get('imagePixelModule', imageId);
            const metadata = {
                BitsAllocated: imagePixelModule.bitsAllocated,
                BitsStored: imagePixelModule.bitsStored,
                Rows: rows,
                Columns: columns,
                WindowCenter: voiLutModule.windowCenter[0],
                WindowWidth: voiLutModule.windowWidth[0],
                PixelData: pixelArray
            };
            const pngBuf = await dcmbufferToPng(metadata);
            const canvas = pngToCanvas(pngBuf);
            document.body.appendChild(canvas);

            console.info('[DEBUG] - Provided array displayed in document below main canvas !');
            resolve(canvas);
        })
    }

    const generatePanoramicControlPoints = async(debug=false) => {
        // Delete measurements that are handmade (thus control points)
        // (Handmade is true because of an old implem where control points were handmade, now they are not
        // but it's a way to identify them)
        const measurements = measurementService.getMeasurements()
            .filter(m => {return m.toolName === 'ArrowAnnotate'});

        const displaySetInstanceUID = getDisplaySetInstanceUID();
        for (const pm of measurements) {
            if (pm.metadata.handmade && pm.displaySetInstanceUID === displaySetInstanceUID) {
                commandsManager.runCommand('deleteMeasurement', { uid: pm.uid });
            }
        }

        const viewport = await getAxialViewport();
        await createControlPoints(viewport, numberBasePoints, { commandsManager, extensionManager, servicesManager });

        /* if (debug) {
            let canvas = await debugShowImage(binarizedArray, layerWidth, layerHeight);
            let ctx = canvas.getContext('2d')
            for (let x = 0; x < nbLayers; x++) {
                drawPoint(ctx, x, f(x), '', 'blue', 2);
            }
            for (const xy of biggestSpatialClusterXy) {
                drawPoint(ctx, xy.x, xy.y, '', 'green', 1);
            }
        }*/
    }

    const generatePanoramicFromControlPoints = async(debug=false) => {
        const viewport = await getAxialViewport();

        setIsProcessingPanoramic(true);
        setProcessingPourcent(50);
        await createPanoramic(viewport, numberBasePoints, { commandsManager, extensionManager, servicesManager });
        setProcessingPourcent(100);
        setIsProcessingPanoramic(false);

        await refreshPanoramicDisplay();
        /* if (debug) {
            debugShowImage(panoramicProcessed, nbLayers, panoramicWidth);
        }*/
    }

    // @ts-ignore
    return (
        <div style={{width:'100%', height:'100%'}}>
            <div style={{width:'100%', display:'flex', flexDirection:'column', height:'100%'}}>
                <p style={{color:'white', fontWeight:'bold', marginBottom:'10px'}}>{'Panoramic Generation'}</p>
                <Button onClick={() =>
                    panoramicService.setAnnotationVisible(getDisplaySetInstanceUID(), !panoramicState?.annotation?.visible)}>
                {`${!panoramicState?.annotation?.visible ? 'Display Control Points' : 'Hide Control Points'}`}</Button>
                { panoramicState?.annotation?.visible &&
                    <>
                        <p style={{color:'white', fontSize:'small', marginBottom:'10px', marginTop: '10px'}}>
                            {'1 - Generate control points from where panoramic will be computed with'}</p>
                        <Button onClick={() => {generatePanoramicControlPoints(false)}}>{'Generate Control Points'}</Button>
                        <p style={{color:'white', fontSize:'small', marginBottom:'10px', marginTop: '10px'}}>
                            {'2 - Adjust the points as needed so that the green line fits best around the white area of the image '
                            + '(along the dental   .The points do not need to be evenly distributed. You can change brightness '
                            + 'and contrast of the image to enlighten the white line with the Window Level tool in toolbar.'}</p>

                        <p style={{color:'white', fontSize:'small', marginBottom:'10px', marginTop: '10px'}}>
                            {'3 - Once the points are well adjusted, you can modify some parameters but be careful : '
                            + 'the lower the parameters are set, the faster the execution, '
                            + 'but the quality of the panoramic view may be compromised.'}</p>
                        <p style={{color:'#5ACCE6', fontSize:'smaller', marginBottom:'5px'}}>{'Steps of computation:'}</p>
                        <div style={{width:'100%', display:'flex'}}>
                            <input
                                type='range'
                                id='dSection'
                                name='dSection'
                                value={`${Math.floor(numberBasePoints)}`}
                                min='5' max='45'
                                style={{width:'70%', marginRight:'5%'}}
                                onChange={(e) => setNumberBasePoints(parseInt(e.target.value))}
                            />
                            <p style={{color:'#5ACCE6', fontSize:'small', width:'25%'}}>{`${numberBasePoints}`}</p>
                        </div>
                        <p style={{color:'white', fontSize:'small', marginBottom:'10px', marginTop: '10px'}}>
                            {'4 - Once the chosen parameters are good, you can initiate the computation'}</p>
                        <Button onClick={() => generatePanoramicFromControlPoints(false)}>{'Compute Panoramic'}</Button>
                    </>
                }
                { isProcessingPanoramic &&
                    <div className={'w-full mt-[10px] mb-[20px]'}>
                        <p style={{color:'white', fontSize:'small', marginBottom:'10px', marginTop: '10px'}}>{'Creating panoramic...'}</p>
                        <ProgressBar
                            className={'w-full mt-[3px]'}
                            bgColor={processingPourcent < 100 ? '#5ACCE6' : '#50C878'}
                            completed={processingPourcent}
                            transitionDuration={'0.05s'}
                            transitionTimingFunction={'linear'}
                        />
                    </div>
                }
                { panoramicState?.panoramic?.width &&
                    <div style={{width:'100%', display:'flex', flexDirection:'column', justifyContent:'space-evenly', height:'18%'}}>
                        <p style={{color:'white', fontWeight:'bold', marginTop: '10px'}}>{'Dentascan Editing'}</p>
                        <p style={{color:'white', fontSize:'small', marginBottom:'10px', marginTop: '10px'}}>
                            {`${panoramicState.annotation.visible ? '5 - ' : ''}` + 'Scroll through the dentascans using this slider. '
                            + 'The central dentascan is represented by the white line on the panoramic view.'}</p>
                        <div style={{width:'90%', display:'flex', height:'10%', flexDirection:'row', justifyContent:'space-between'}}>
                            <p style={{color:'white', fontSize:'smaller'}}>{'Section:'}</p>
                            <input
                                type='range'
                                id='dSection'
                                name='dSection'
                                value={`${Math.floor(panoramicState.dentascanViewportData.data[0].initialImageIndex)}`}
                                min='0' max={`${panoramicState.panoramic.width - 1}`}
                                style={{width:'70%'}}
                                onChange={(e) => panoramicService.setInitialImageIndex(
                                    getDisplaySetInstanceUID(),
                                    Math.floor(Number(e.target.value))
                                )}
                            />
                        </div>
                    </div>
                }
            </div>
        </div>
    );
}
